/* src/styles/Updates.css */
.updates-page {
  max-width: 1120px;
  margin: 0 auto;
  padding: 40px 24px 56px;
  color: #f5f5ff;
}

/* Scroll reveal */
.reveal {
  opacity: 0;
  transform: translateY(40px);
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.reveal.reveal-visible {
  opacity: 1;
  transform: translateY(0);
}

/* Hero */
.updates-hero h1 {
  font-size: 2.2rem;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 0.15em;
}

.updates-hero p {
  font-size: 0.95rem;
  color: #c4c7e9;
  line-height: 1.7;
  max-width: 720px;
}

/* Sections */
.updates-section {
  margin-top: 40px;
}

.updates-section h2 {
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  margin-bottom: 20px;
}

/* Cards grid */
.updates-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 18px;
}

.updates-grid.single {
  grid-template-columns: minmax(0, 1fr);
}

.updates-card {
  background: radial-gradient(circle at top, #1a1b33 0, #090a16 65%);
  border-radius: 18px;
  padding: 16px 18px;
  border: 1px solid rgba(255, 255, 255, 0.06);
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.75);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.updates-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 24px 60px rgba(37, 99, 235, 0.4);
}

.updates-card h3 {
  font-size: 1rem;
  margin-bottom: 6px;
  color: #81b2f1;
}

.updates-card p {
  font-size: 0.9rem;
  color: #c4c7e9;
  line-height: 1.6;
}

/* Responsive */
@media (max-width: 768px) {
  .updates-page {
    padding: 32px 16px 48px;
  }
}
/* ========================================
   UNIVERSAL RESPONSIVE BASE - PASTE THIS FIRST
   ======================================== */

/* Reset & Base */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.6;
  overflow-x: hidden;
}

/* Universal Responsive Typography */
h1 { font-size: clamp(2rem, 5vw, 3.5rem); line-height: 1.1; margin: 0 0 1rem 0; }
h2 { font-size: clamp(1.75rem, 4vw, 2.75rem); line-height: 1.2; margin: 0 0 0.75rem 0; }
h3 { font-size: clamp(1.25rem, 3vw, 1.875rem); line-height: 1.3; margin: 0 0 0.5rem 0; }
h4 { font-size: clamp(1.125rem, 2.5vw, 1.5rem); line-height: 1.4; margin: 0 0 0.5rem 0; }
p { margin: 0 0 1rem 0; }

/* Universal Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

/* Responsive Padding */
@media (max-width: 1200px) { .container { padding: 0 1.25rem; } }
@media (max-width: 768px)  { .container { padding: 0 1rem; } }
@media (max-width: 480px)  { .container { padding: 0 0.75rem; } }

/* Universal Flex/Grid Helpers */
.flex { display: flex; gap: 1rem; }
.flex-col { flex-direction: column; }
.grid { display: grid; gap: 1.5rem; }
.grid-cols-2 { grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); }

/* Responsive Images */
img, video {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Buttons - Universal */
.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: 600;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: clamp(0.875rem, 2vw, 1rem);
}

.btn:hover { transform: translateY(-1px); }

/* Responsive Text */
.text-sm   { font-size: clamp(0.75rem, 2vw, 0.875rem); }
.text-base  { font-size: clamp(0.875rem, 2.5vw, 1rem); }
.text-lg    { font-size: clamp(1rem, 3vw, 1.125rem); }
.text-xl    { font-size: clamp(1.125rem, 3.5vw, 1.25rem); }

/* ========================================
   RESPONSIVE BREAKPOINTS - UNIVERSAL
   ======================================== */

/* Large Desktop */
@media (min-width: 1200px) { 
  body { font-size: 17px; } 
}

/* Desktop */
@media (max-width: 1199px) and (min-width: 992px) { }

/* Tablet */
@media (max-width: 991px) and (min-width: 768px) { 
  h1 { font-size: 2.5rem; }
  h2 { font-size: 2rem; }
}

/* Mobile Large */
@media (max-width: 767px) and (min-width: 481px) { 
  .flex { flex-direction: column; }
  .grid-cols-2 { grid-template-columns: 1fr; }
}

/* Mobile Small */
@media (max-width: 480px) { 
  body { font-size: 15px; padding: 0 0.5rem; }
  .hero-title { font-size: 2rem !important; }
  .btn { padding: 0.625rem 1.25rem; }
}

/* Tiny Screens */
@media (max-width: 360px) { 
  body { font-size: 14px; }
  .container { padding: 0 0.5rem; }
}

/* Landscape Mobile Fix */
@media (max-height: 500px) and (orientation: landscape) {
  body { font-size: 14px; }
  h1 { font-size: 1.75rem; }
}

/* High DPI Displays */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  img { image-rendering: -webkit-optimize-contrast; }
}
